.app {
  height:100%;
  justify-content: center;
  align-items: center;
}

.rectangle {
  display: flex;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px; 
}

.control {
  padding-bottom:12px;
  margin-bottom: 2px;
  background-color: linen;
}

.checklist-container {
  height: 916px;
  width: 370px;
  background-color: white;
  border-radius: 10px;

  display: flex;
  align-items: center;
  flex-direction: column;
}

.checklist {
  margin-top:40px;
  list-style-type: none;
  padding: 0;
}

.checklist li {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.separador-check {
  margin-right: 70px;
  width: 200px;
  display: flex;
  flex-direction: column;
}

.separador-check-dccdce {
  display: flex;
  flex-direction: column;
}

.checklist-text {
  font-size: 2rem; /* Tamaño del texto relativo a la pantalla */
  color: black;
  margin-right: 35px; /* Espacio entre el texto y el check */
}

.checklist-subtext {
  font-size: 1.3rem; /* Tamaño del texto relativo a la pantalla */
  color: black;
  margin-top: 5px; /* Espacio entre el texto y el check */
}

.checklist-label {
  display: flex;
  align-items: center;
  position: relative;
}

.checklist-label input[type="checkbox"] {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.checkmark {
  width: 24px; /* Tamaño del checkmark */
  height: 24px; /* Tamaño del checkmark */
  border: 2px solid black;
  border-radius: 4px; /* Hace que el checkmark sea un cuadrado */
  position: relative;
}

.checkmark::after {
  content: '✓'; /* Agrega un tick (✓) al checkmark */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 23px; /* Tamaño del tick relativo al checkmark */
  color: green;
  opacity: 0;
}

.checklist-label input[type="checkbox"]:checked + .checkmark::after {
  opacity: 1;
}

