.selectMoneda{
  width: 100%; /* Ancho fijo del select */
  white-space: nowrap; /* Evita el salto de línea */
  overflow: hidden; /* Oculta el exceso de contenido */
  text-overflow: ellipsis; /* Muestra puntos suspensivos para el texto truncado */
}

#montoUSD{
  font-weight: bold;
}

.header {
  position: sticky;
  top: 0;
  background-color: #f2f2f2;
  z-index: 1;
  display: flex;
}

.header table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  height: 10px;
}

.header th {
  position: sticky;
  top: 0;
  background-color: #fff3e6;
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

.header th:not(:last-child) {
  border-right: 1px solid #ddd;
  margin-right: 8px;
}

.header td:not(:last-child) {
  border-right: 1px solid #ddd;
  margin-right: 8px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.header td {
  height: 25px;
  top: 0;
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

th,
td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

.containerLista{
